import React from 'react'
import css from './PromoBlockMobile.module.scss'
import PromoListMobile from 'components/PromoList/PromoListMobile'

const PromoBlockMobile = ({ className }) => {
  const promoData = [
    {
      type: 'teeth',
      url: '/',
      color: '#f4785d',
      duration: '  24 ',
      title: ' ',
      price: ' <span>20 000</span> '
    },
    {
      type: 'implant-single',
      url: '/',
      color: '#c58ddf',
      duration: '  24 ',
      title: ' &nbsp;',
      price: ' <span>95 000</span> '
    },
    {
      type: 'implants-jaw',
      url: '/',
      color: '#c58ddf',
      duration: '  24 ',
      title: '    ',
      price: ' <span>120 000</span> '
    },
    {
      type: 'implant-single',
      url: '/',
      color: '#7486e6',
      duration: '  24 ',
      title: ' &nbsp;',
      price: ' <span>95 000</span> '
    },
  ]


  return (
    <div className={className}>
      <h3 className={css.title}>
        
      </h3>
      <PromoListMobile data={promoData} className={css.promo} />
    </div>
  )
}

export default PromoBlockMobile
